body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #e2e1e0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.header {
  padding: 20px;
  font-size: 24px;
  font-weight: bold;
  background-color: #24292e;
  color: #f2f2f2;
  position: fixed;
  width: 100%;
  top: 0;
}

.title,
.title:hover,
.title:focus,
.title:active {
  color: #24292e;
}

.newsDate {
  border-bottom: 1px solid grey;
  padding-bottom: 20px;
}


.pagination {
  display: -webkit-flex;
  display: flex;
  padding: 30px 0;
  width: 100%;
  overflow: auto;
}

.pageIndex {
  color: black;
  padding: 8px 16px;
  text-decoration: none;
  -webkit-transition: background-color .3s;
  transition: background-color .3s;
  margin-left: 1px;
}

.pageIndex.active {
  background-color: #24292e;
  color: white;
}

.pageIndex:hover:not(.active) {
  background-color: #ddd;
}

.dashboard {
  width: 80%;
  margin: 100px auto;
  max-width: 720px;
  border-radius: 2px;
  padding: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background: #fff;
  min-height: 100vh;
}

.dashBoardTitle {
  text-align: center;
}


